<template>
  <div>
    <div v-if="getKey">
      <GmapMap
      ref="gmap"
      :center="getCenter()"
      :zoom="17"
      style="height: 500px;margin: 0px -8px;"
      class="no-padding"
      :options="getOptions()"
    >
      <GmapMarker
        :key="index"
        v-for="(m, index) in getMarkers()"
        :position="m.position"
        :clickable="true"
        :draggable="false"
        :title="m.title"
        :icon="m.icon"
        @click="clicked()"
      />
    </GmapMap>
    <div style="text-align:center;margin-top:10px">
      <h4>Girilen Lat-Lon Konumları :</h4>
      <p v-for="(item,index) in mapData" :key="index">
     {{index+1}} - {{item.lat}} - {{item.lon}}
      </p>
    </div>
    </div>
    <div style="text-align:center" v-else>
      <input type="text" v-model="key" name="" id="">
      <button @click="setAllLatLon()"> Key Gönder </button>
    </div>
  </div>
</template>
<script>

export default {
  name: "SiteMap",
  props: [],
  data(){
    return{
      mapData:[],
      getKey:false,
      key:""
    }
  },
  methods: {   
    getOptions() {
      return {
        zoomControl: true,
        mapTypeControl: false,
        scaleControl: false,
        streetViewControl: false,
        rotateControl: false,
        fullscreenControl: false,
        disableDefaultUi: false
      };
    },
    clicked() {
     // after click
    },
    getCenter() {     
          return {
            lat: 38.451367,
            lng: 27.187302
          };
    },
    getMarkers() {
      // generating markers for site map
        var markers = [];
        // remove this after lat long received from api.
        const tempLatLong=[]
        this.mapData.forEach(el=>{
          tempLatLong.push({lat:Number(el.lat), lng:Number(el.lon)})
        })
        console.log(tempLatLong);
        
        // const tempLatLong = [
        //   { lat: 37.9068361, lng: -122.116971 },
        //   { lat: 37.9168362, lng: -122.076972 },
        //   { lat: 37.9268363, lng: -122.136973 },
        //   { lat: 37.9368364, lng: -122.146974 },
        //   { lat: 37.9468365, lng: -122.106975 },
        //   { lat: 37.9568366, lng: -122.166976 },
        //   { lat: 37.9668367, lng: -122.176977 },
        //   { lat: 37.9768368, lng: -122.016978 },
        //   { lat: 37.9868369, lng: -122.196979 }
        // ];
       for(let i=0;i<tempLatLong.length;i++){
          markers.push({
              position: tempLatLong[i],
              title: 'test title'  // if you want to show different as per the condition.         
            });
        }
        return markers;      
    },
    setAllLatLon(){
         var requestOptions = {
  method: 'GET',
  redirect: 'follow'
};

fetch("https://eve-soyle.firebaseio.com/latlon.json", requestOptions)
  .then(response => response.json())
  .then(result => {
     console.log(result);
    this.mapData = result
    this.getKey= true
  })
  .catch(error => console.log('error', error));
    }
  },
  components: {},
  created() {},
  mounted() {
   
  }
};
</script>
